<template>
  <div class="restrict news">
    <div class="row">
      <div class="maxTitle">
        <div>NEWS</div>
      </div>
      <ul class="newslist">
        <li v-for="(i, j) in newslist" :key="j">
          <div class="newsdate">
            <span>{{ i.time }}</span>
          </div>
          <div class="newsbrief">
            <a href="">{{ i.title }}</a>
          </div>
        </li>
      </ul>
      <input
        type="button"
        class="ReadMoreBtn"
        name=""
        @click="getMore()"
        id="anniu"
        value="READ MORE >"
        style="margin: 70px auto"
      />
    </div>
  </div>
</template>

<script>
export default {
    data: () => ({
        newslist: []
    }),
    created() {
        var news = this.$store.state.news
        this.newslist = news.slice(0, 8)
    },
    methods: {
        getMore: function(){
            var news = this.$store.state.news

            if (this.newslist.length < news.length) {
                this.newslist = news.slice(0, this.newslist.length + 5)
            }
        }
    }
};
</script>
